<template>
  <div class="blog-show">
    <van-nav-bar :title="blog.title" left-text="返回" left-arrow fixed @click-left="$router.go(-1)" />
    <div class="article">
      <div class="article-header">
        <div class="title">{{ blog.title }}</div>
        <div class="article-author-edit">
          <div class="article-author">
            <div class="img">
              <img v-img:avatar :src="baseIMGURL + userid + '?byuserid=1'" class="ueser-head" />
            </div>
            <div class="user-name-time">
              <span class="user-name">{{ blog.username }}</span>
              <span class="time">
                {{
                blog.createdAt | date1
                }}
              </span>
            </div>
          </div>
          <div class="article-edit">
            <span class="views iconfont icon-Icon-guankanliang">
              {{
              blog.views
              }}
            </span>
            <span
              @click.stop="handleLiked"
              :class="[
                                'like',
                                'iconfont',
                                liked ? 'icon-shoucang' : 'icon-shoucang1'
                            ]"
            >{{ likes.length }}</span>
            <!-- <span
                            class="edit"
                            @click.stop="handleEdit"
                            v-show="user._id === blog.userid"
                        >
                            编辑
            </span>-->
          </div>
        </div>
      </div>
      <div class="markdown-body article-detail" v-html="html"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blog: {
        html: ""
      },
      likes: []
    };
  },
  mounted() {
    let id = this.$route.params.id;
    this.getBlogById(id);
  },
  computed: {
    html() {
      return this.blog.html;
    },
    userid() {
      return this.blog.userid || "default.png";
    },
    liked() {
      return this.likes.includes(this.user._id);
    }
  },
  methods: {
    async getBlogById(id) {
      let blog = await this.$store.dispatch("getBlogById", id);
      this.blog = blog;
      this.likes = blog.likes;
    },
    handleEdit() {
      this.$router.push({
        path: "/add-blog?type=edit&id=" + this.blog._id
      });
    },
    async handleLiked() {
      let likes = await this.$store.dispatch("toggleLike", {
        blogID: this.blog._id,
        userID: this.user._id
      });
      this.likes = likes;
    }
  }
};
</script>

<style lang="scss" scoped>
.blog-show {
  width: 100%;

  .article {
    margin: 0 auto;
    padding-top: 46px;
    box-sizing: border-box;
    max-width: 962px;
    background-color: #fff;
    width: 100%;
    div.article-header {
      padding: 0.25rem;
      box-sizing: border-box;
      z-index: 9;
      background-color: #fff;
      width: 100%;
      div.title {
        letter-spacing: 0;
        line-height: 40px;
        font-size: 24px;
        font-weight: bold;
        margin: 18.76px 0;
      }

      div.article-author-edit {
        font-size: 14px;
        min-height: 40px;
        padding: 0.5rem;
        border-bottom: 1px solid #f5f6f7;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        div.article-author {
          display: flex;
          flex-direction: row;
          align-items: center;
          position: relative;
          .img {
            margin-right: $uni-spacing-col-sm;
            width: 30px;
            height: 30px;
            img.ueser-head {
              width: 30px;
              height: 30px;
              border-radius: $uni-border-radius-circle;
            }
          }
          span {
            margin-right: $uni-spacing-col-base;
            display: block;
          }
          .user-name-time {
            display: flex;
            flex-direction: column;
            span.user-name {
              white-space: nowrap;
              margin-bottom: 4px;
              font-size: 16px;
              font-weight: bold;
            }

            span.time {
              font-size: 12px;
              white-space: nowrap;
              color: $uni-text-color-grey;
            }
          }
        }

        div.article-edit {
          padding: 0.5rem;
          padding-left: 38px;
          span {
            padding: 0 5px;
          }
          span.views {
            white-space: nowrap;
            color: $uni-color-primary;
            font-size: 14px;
            padding-left: 0;
            &::before {
              color: $uni-text-color;
              font-size: 16px;
            }
          }

          span.like {
            white-space: nowrap;
            cursor: pointer;
            border-left: 1px solid $uni-bg-color-grey-more;
            color: $uni-text-color-grey;
            font-size: 14px;
            &::before {
              color: $uni-color-error;
              font-size: 16px;
            }
          }
          .edit {
            border-left: 1px solid $uni-bg-color-grey-more;
            letter-spacing: 3px;
            cursor: pointer;
            color: $uni-color-primary;
            &:hover {
              color: $uni-color-error;
            }
          }
        }
      }
    }
    .article-detail {
      padding: 0.25rem;
    }
  }
}
</style>